@import './media.scss';

@mixin H1 {
  font-weight: 700;
  font-size: 96px;
  line-height: 109%;
  letter-spacing: 0.02em;
  @include media(sm) {
    font-size: 34px;
    line-height: 112%;
    letter-spacing: 0.02em;
  }
}
@mixin H2 {
  font-weight: 700;
  font-size: 59px;
  line-height: 124%;
  @include media(sm) {
    font-size: 34px;
    line-height: 112%;
    letter-spacing: 0.02em;
  }
}
@mixin H3 {
  font-weight: 500;
  font-size: 35px;
  line-height: 122%;
  @include media(sm) {
    font-weight: 700;
    font-size: 23px;
    line-height: 120%;
    letter-spacing: 0.04em;
  }
}
@mixin Big {
  font-weight: 700;
    font-size: 23px;
    line-height: 122%;
    @include media(sm) {
      font-size: 16px;
      line-height: 142%;
    }
}
@mixin Normal {
  font-weight: 400;
  font-size: 20px;
  line-height: 142%;
  @include media(sm) {
    font-size: 16px;
    line-height: 142%;
  }
}
@mixin Small {
  font-weight: 400;
  font-size: 17px;
  line-height: 142%;
  letter-spacing: -0.01em;
  @include media(sm) {
    font-size: 16px;
    line-height: 142%;
  }
}
